.mobile-nav{
    position: fixed;
    z-index: 100;
    top: 50px;
    height: 100%;
    display: flex;  
    flex-direction: column;
    max-width: 320px;
    width: 100%;
    padding: 50px 30px 30px 10px;
    background-color: rgba(var(--bg-a),1.0);
    transform: translateX(-120%);
    transition: all .4s;
    grid-gap: 20px;
    
    &.active{
        transform: translateX(0);
    }

    // @include for-tablet{
    //     max-width: clamp(320px, 32vw, 480px);
    //     padding: clamp(30px, 4.867vw, 73px) 30px 30px clamp(20px, 3.133vw, 47px);
    //     transition: all .4s ease-out;

    //     &::before{
    //         position: absolute;
    //         content: '';
    //         top: 0;
    //         background-color: rgba(var(--bg-o),1.0);
    //         height: 100%;
    //         right: 0;
    //         width: 1px;
    //     }

    //     &::after{
    //         left: 0;
    //         width: 100%;
    //         height: 1px;
    //         position: absolute;
    //         content: '';
    //         top: 0;
    //         background-color: rgba(var(--bg-o),1.0);
    //     }
    // }

    &__list{
        display: flex;
        flex-direction: column;
        grid-gap: 20px;
    }

    &__title{
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: rgba(var(--cl-k),1.0)
    }

    &__link{
        display: flex;
        align-items: center;
        grid-gap: 10px;
        font-family: var(--font-family);
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: rgba(var(--cl-b),1.0);
        position: relative;
    }

}