// --------------- hex to rgba mixin ---------------
@function HexToRGB($hex) {
  @return red($hex), green($hex), blue($hex);
}
:root {
// --------------- default global variable ---------------
  --body-bg: #ffffff;
  // --footer-bg: #0C1323;
  --body-font-size: 16px;
  --content-width: 1500px;
  --content-width-mode: 1595px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-mode: calc(var(--content-width-mode) + (var(--container-offset) * 2));

// --------------- default JS variable ---------------
  --header-height: 50px;

// --------------- default scroll variable ---------------
  --scrollbar-thumb: #1D252D;
  --scrollbar-thumb-hover: #f6866F6ff;
  --scrollbar-track:  #ffffff;
  --scrollbar-track-hover: #1D252D;
  --overlay-bg: rgba(0, 0, 0, 0.5);

// --------------- default slect variable ---------------
  --select-inner-bg: #404040;
  --select-inner-border: #FDFDFD;
  --select-focus-border: #FDFDFD;

// --------------- default font-family variable ---------------
--font-family: "Gotham", sans-serif; // var(--font-family);
--font-family2: "Roboto", sans-serif; // var(--font-family2);
--font-family3: "Futura-PT", sans-serif; // var(--font-family3);
--font-family4: "Raleway", sans-serif; // var(--font-family4);

// --------------- default text color variable ---------------
  // --burger-cl: #{HexToRGB(#00d655)};

