.registration-section{
    position: relative;
    width: 100%;

    margin-bottom: clamp(56px, 6.133vw, 92px);

    &__background{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
        height: 100%;
    

        img,picture{
            width: 100%;
            height: 100%;
            object-fit: cover;
            
        }
    }

    &__box{
        // height: clamp(680px, 50.667vw, 760px);
        padding:clamp(40px, 5.333vw, 80px) 0;
    }
}